    height: 90%;
    text-align: center;
    padding-top: 50px;
    width: 100%;
  }

  .bottomofbottom{
    height: 10%;
    text-align: center;
    width: 100%;
  }


  /** Settings for image-buttons in bottom-component
  *
  *
  */
  .github{
    width: 20vw;
  }

  .linkedin{
    width: 40vw;
  }

  .linkedin:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }

  .github:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  } 
}
